body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

* {
  line-height: 1.7;
  font-family: 'Roboto', sans-serif;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 24px;
}

h3 {
  font-size: 19px;
}

h4 {
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: .5rem;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
}

.top-nav {
  width: 100%;
  background-color: #ff0;
  padding: 3px 0;
}

.top-bar {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.top-bar ul {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
}

.top-bar ul li {
  display: inline;
}

.top-menu-nav {
  display: flex;
  justify-content: space-between;
}

.top-menu-nav ul li {
  display: inline;
  padding-left: 20px;
}

.top-menu {
  width: 100%;
  background-color: #1e262d;
  padding: 0 0;
}

.wrapper-padding {
  padding: 100px 0;
}

.product {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.959);
}

.wrapper-bkg {
  width: 100%;
  background-color: #f7db5e;
}

.products {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-align: center;
  margin-top: 50px;
}

.products li {
  border: #ff0 1px solid;
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(20% - 10px);
}

.advantage {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-align: center;
  margin-top: 50px;
}

.advantage li {
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
}

.stages-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-align: center;
  margin-top: 50px;
}

.stages-list li {
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(25% - 10px);
}

#slider_partners,
#slider_reviews {
  margin-top: 30px;
}

.footer-bkg {
  width: 100%;
  padding: 50px 0;
  background-color: antiquewhite;
}

.footer-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.footer-list div {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
  display: flex;
  flex-direction: column;
  justify-self: flex-end;
}

.footer-list div p:first-child {
  padding: 0 0 30px 0;
}

.footer-link {
  display: flex;
}

.footer-link ul {
  margin: 0 0 0 auto;
}

#header {
  line-height: 1.5;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  color: #fff;
}

#header a {
  text-decoration: none;
  color: #fff;
}

#header {
  background: #1E262D;
  width: 100%;
  position: relative;
}

#header:after {
  content: "";
  clear: both;
  display: block;
}

.search {
  float: right;
  padding: 30px;
}

input {
  border: none;
  padding: 10px;
  border-radius: 20px;
}

.logo {
  float: left;
  padding: 15px 0 15px;
}

.logo a {
  font-size: 23px;
  display: block;
}

nav {
  float: right;
}

nav > ul {
  float: left;
  position: relative;
}

nav li {
  list-style: none;
  float: left;
}

nav .dropdown {
  position: relative;
}

nav li a {
  float: left;
  padding: 23px;
}

nav li a:hover {
  background: #2C3E50;
}

nav li ul {
  display: none;
}

nav li:hover ul {
  display: inline;
}

nav li li {
  float: none;
}

nav .dropdown ul {
  position: absolute;
  left: 0;
  top: 100%;
  background: #34495e;
  padding: 10px 0;
  border-bottom: 3px solid #34495e;
  z-index: 10;
}

nav .dropdown li {
  white-space: nowrap;
  z-index: 10;
}

nav .dropdown li a {
  padding: 10px 35px;
  font-size: 13px;
  min-width: 200px;
}

nav .mega-dropdown {
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  overflow: hidden;
  padding: 20px 35px;
  border-bottom: 3px solid #34495e;
}

nav li li a {
  float: none;
  color: #333;
  display: block;
  padding: 8px 10px;
  border-radius: 3px;
  font-size: 13px;
}

nav li li a:hover {
  background: #333;
}

.mega-col {
  width: 25%;
  float: left;
}

#menu-icon {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -12px;
  display: none;
}

#menu-icon span {
  border: 2px solid #fff;
  width: 30px;
  margin-bottom: 5px;
  display: block;
  -webkit-transition: all .2s;
  transition: all 0.1s;
}

.bradcrams li {
  float: left;
}

.bradcrams li::after {
  content: "/";
}

.bradcrams li:last-child::after {
  content: "";
}

.bradcrams li > a:last-child {
  color: #1e262d;
}

.product-img-text {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.product-img-text .product-img {
  flex: 0 1 30%;
}

.product-img-text .product-text {
  flex: 0 1 70%;
}

.dop-info ul {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.dop-info ul li {
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
}

.work-img {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.work-img li {
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(25% - 10px);
}

@media only screen and (min-width: 960px) {
  nav {
    display: block !important;
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .product-img-text .product-img {
    flex: 0 1 50%;
  }

  .product-img-text .product-text {
    flex: 0 1 50%;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .products li {
    width: calc(25% - 10px);
  }

  .advantage li {
    width: calc(33.333333333333336% - 10px);
  }

  .stages-list li {
    width: calc(25% - 10px);
  }

  .footer-list div {
    width: calc(33.333333333333336% - 10px);
  }

  .product-img-text .product-img {
    flex: 0 1 30%;
  }

  .product-img-text .product-text {
    flex: 0 1 70%;
  }

  .dop-info ul li {
    width: calc(33.333333333333336% - 10px);
  }

  .work-img li {
    width: calc(33.333333333333336% - 10px);
  }
}

@media only screen and (max-width: 990px) {
  nav > ul > li > a {
    padding: 35px 15px;
  }
}

@media only screen and (max-width: 959px) {
  nav {
    display: none;
    width: 100%;
    clear: both;
    float: none;
    max-height: 400px;
    overflow-y: scroll;
  }

  #menu-icon {
    display: inline;
    top: 35px;
    cursor: pointer;
  }

  #menu-icon.active .first {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    margin-top: 10px;
  }

  #menu-icon.active .second {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    position: relative;
    top: -9px;
  }

  #menu-icon.active .third {
    display: none;
  }

  .search {
    float: none;
  }

  .search input {
    width: 100%;
  }

  nav {
    padding: 10px;
  }

  nav ul {
    float: none;
  }

  nav li {
    float: none;
  }

  nav ul li a {
    float: none;
    padding: 8px;
    display: block;
  }

  #header nav ul ul {
    display: block;
    position: static;
    background: none;
    border: none;
    padding: 0;
  }

  #header nav a {
    color: #fff;
    padding: 8px;
  }

  #header nav a:hover {
    background: #fff;
    color: #333;
    border-radius: 3px;
  }

  #header nav ul li li a:before {
    content: "- ";
  }

  .mega-col {
    width: 100%;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .products li {
    width: calc(50% - 10px);
  }

  .advantage li {
    width: calc(50% - 10px);
  }

  .stages-list li {
    width: calc(50% - 10px);
  }

  .footer-list div {
    width: calc(50% - 10px);
  }

  .footer-link ul {
    padding: 40px 0 0 0;
    margin: 0 0 0 0;
  }

  .dop-info ul li {
    width: calc(50% - 10px);
  }

  .work-img li {
    width: calc(50% - 10px);
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .products li {
    width: calc(100% - 10px);
  }

  .advantage li {
    width: calc(100% - 10px);
  }

  .stages-list li {
    width: calc(100% - 10px);
  }

  .footer-list div {
    width: calc(100% - 10px);
  }

  .dop-info ul li {
    width: calc(100% - 10px);
  }

  .work-img li {
    width: calc(100% - 10px);
  }
}